function bpcontentFadeUp() {
    var bpcontent = document.querySelectorAll(".bp-content");
    for (var i = 0; i < bpcontent.length; i++) {
        var windowHeight = window.innerHeight;
        var elementTop = bpcontent[i].getBoundingClientRect().top;
        var elementVisible = 150;
        if (elementTop < windowHeight - elementVisible) {
            bpcontent[i].classList.add("active");
        }
    }
}
window.addEventListener("scroll", bpcontentFadeUp);

// To check the scroll position on page load
bpcontentFadeUp();